#dashboard-container {
    left: 0;
    right: 0;
    margin: auto;
    margin-top: 100px;
    width: 80vw;
    height: auto;
}

#dashboard-container h1 {
    margin: 0;
    padding: 0;
    margin-left: 1vw;
    margin-bottom: 25px;
    font-family: 'Orbitron', 'sans-serif';
}

@media screen and (max-width: 910px) {
    #dashboard-container {
        width: 90vw;
    }
}